@font-face {
  font-family: snackcontent;
  src: url("./fonts/MyriadPro-Regular.otf");
}

* {
  padding: 0;
  margin: 0;
  font-family: snackcontent;
  box-sizing: border-box;
  min-height: 0;
  min-width: 0;
  outline: none;
}
.slider-overlay a:hover {
  color: #FFF
}
.container {
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 576px) {
  .container {
   width: 85%;
  }
  .hero-slider .slick-dots {
    left: calc(((100% - 85%) / 2) + 15px);
  }

}
@media (min-width: 768px) {
  .container {
    width: 85%;
  }
  .hero-slider .slick-dots {
    left: calc(((100% - 85%) / 2) + 15px);

  }
}
@media (min-width: 992px) {
  .container {
    width: 85%;
  }
  .hero-slider .slick-dots {
    left: calc(((100% - 85%) / 2) + 15px);

  }
}
@media (min-width: 1200px) {
  .container {
    width: 1100px;
  }
  .hero-slider .slick-dots {
    left: calc(((100% - 1100px) / 2) + 15px);

  }
}

.left--second .slick-dots li button:focus,
.left--second .slick-dots li button:hover,
.left--second .slick-dots {
  display: none !important;
}

.second--secion .slick-dots li button:focus,
.second--secion .slick-dots li button:hover,
.second--secion .slick-dots {
  display: none !important;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
a {
  text-decoration: none;
}
