.add--comment {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 30px;
}
.add--comment img {
  height: 70px;
  width: 70px;
  border-radius: 50%;
}

.add--comment button {
  height: 42px;
  width: 100%;
  border: 1px solid #dbdbdb;
  background-color: #ffffff;
  background: linear-gradient(90deg, #da0842 0%, #8c0080 100%);
  font-size: 18px;
  font-weight: 100;
  text-transform: unset;
  margin-top: 14px;
  cursor: pointer;
}
