.NewsContainer {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-column-gap: 30px;
}
.newsPage {
  padding-top: 40px;
}
.newsPage .adv {
  width: 100%;
  height: 90px;
  margin: 40px auto;
}
.seperate {
  border: none;
  height: 2px;
  background-color: #dbdbdb;
  margin: 30px 0;
}
