.input-field {
  font-size: 16px;
  padding: 0 15px;
  margin: 0 30px 20px 0;
  height: 38px;
  width: 100%;
  background-color: #2f2f2f;
  border-radius: 5px;
  color: #8f8f8f;
  font-size: 14px;
  border: none;
  margin-bottom: 20px;
}
.spec--input {
  height: 42px;
  width: 100%;
  border: 1px solid #dadada;
  background-color: #ffffff;
  padding-left: 14px;
  margin-bottom: 20px;
}
.spec--input::placeholder {
    color: #C4C4C4;
    font-size: 14px;
    font-weight: 100;
}