.pop--post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 16px;
    margin-bottom: 21px;
}

.pop--post .image--post img {
    height: 100px;
}

.pop--post .post--data h3 {
    color: #212121;
    font-size: 18px;
    margin-bottom: 16px;
}

.pop--post .post--data span {
    color: #A0A0A0;
    font-size: 14px;
}