.second--nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
}

.second--nav h2 {
    background: linear-gradient(90deg, #DA0842 0%, #8C0080 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 36px;
    margin-right: 15px;
    margin-bottom: 20px;
    cursor: pointer;
}

.second--nav img {
    width: 728px;
    height: 90px;
}